body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-roboto: Roboto;
  --font-inherit: inherit;

  /* font sizes */
  --font-size-xl: 20px;
  --font-size-lg: 18px;
  --font-size-3xl: 22px;
  --font-size-6xl: 25px;
  --font-size-13xl: 32px;
  --font-size-2xl: 21px;
  --font-size-9xl: 28px;
  --font-size-5xl: 24px;

  /* Colors */
  --color-black: #000;
  --color-white: #fff;
  --color-darkslateblue: #65458c;
  --color-gray-100: #8c8c8c;
  --color-gray-200: #281e34;
  --color-dimgray: #646464;
  --color-gainsboro: #d9d9d9;
  --color-silver: #c0c0c0;

  /* Paddings */
  --padding-5xl: 24px;
  --padding-14xl: 33px;

  /* border radiuses */
  --br-10xs: 3px;
  --br-8xs: 5px;
}
